.previewSitePage,
.previewViewPort,
.decorativeBackSplash {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.previewSitePage{
    height: 100%;
}
.previewSitePage h1{
    font-size: 30px;
    margin: 0 30px 35px;
}

.previewViewPort {
    padding: 30px;
    background-color: plum;
}

.decorativeBackSplash{
    background-color:rgba(255, 255, 255, 0.2);
    border: 5px double rgba(255, 255, 255, 0.2);
    padding: 50px;
}
.previewViewPort ul {
    display: flex;
    font-size: 19px;
}
.previewViewPort li,
.previewViewPort h2,
.previewViewPort h3,
.previewViewPort h4 {
    margin: 10px;
    text-shadow: 1px 1px 0px rgba(173, 173, 173, 0.13);
}

.previewViewPort h2 {
    font-size: calc(20px + .2vw);
}

.previewViewPort h3 {
    font-size: calc(16.5px + .2vw );
}

.previewViewPort h4 {
    font-size: calc(15px + .2vw )
}

